.footer {
    background: #f6f9ff;
    padding: 0 0 30px 0;
    font-size: 14px;
  }
  .footer .footer-top {
    background: white url('https://cdn.dribbble.com/userupload/8537781/file/original-b308fa89b9ec32924cdd8298df11cfc9.png?compress=1&resize=752x') no-repeat right top;
    background-size: contain;
    border-top: 1px solid #e1ecff;
    border-bottom: 1px solid #e1ecff;
    padding: 60px 0 30px 0;
  }
  @media (max-width: 992px) {
    .footer .footer-top {
      background-position: center bottom;
    }
  }
  .footer .footer-top .footer-info {
    margin-bottom: 30px;
  }
  .footer .footer-top .footer-info .logo {
    line-height: 0;
    margin-bottom: 20px;
    text-decoration: none;
  }
  .footer .footer-top .footer-info .logo img {
    max-height: 30px;
    margin-right: 6px;
  }
  .footer .footer-top .footer-info .logo span {
    font-size: 30px;
    font-weight: 700;
    letter-spacing: 1px;
    color: #012970;
    font-family: "Nunito", sans-serif;
    margin-top: 3px;
  }
  .footer .footer-top .footer-info p {
    font-size: 14px;
    line-height: 24px;
    margin-bottom: 0;
    font-family: "Nunito", sans-serif;
  }
  .footer .footer-top .social-links a {
    font-size: 20px;
    display: inline-block;
    color: rgba(1, 41, 112, 0.5);
    line-height: 0;
    margin-right: 10px;
    transition: 0.3s;
  }
  .footer .footer-top .social-links a:hover {
    color: #012970;
  }
  .footer .footer-top h4 {
    font-size: 16px;
    font-weight: bold;
    color: #012970;
    text-transform: uppercase;
    position: relative;
    padding-bottom: 12px;
  }
  .footer .footer-top .footer-links {
    margin-bottom: 30px;
  }
  .footer .footer-top .footer-links ul {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  .footer .footer-top .footer-links ul i {
    padding-right: 2px;
    color: #d0d4fc;
    font-size: 12px;
    line-height: 0;
  }
  .footer .footer-top .footer-links ul li {
    padding: 10px 0;
    display: flex;
    align-items: center;
  }
  .footer .footer-top .footer-links ul li:first-child {
    padding-top: 0;
  }
  .footer .footer-top .footer-links ul a {
    color: #013289;
    transition: 0.3s;
    display: inline-block;
    line-height: 1;
    text-decoration: none;
  }
  .footer .footer-top .footer-links ul a:hover {
    color: #4154f1;
  }
  .footer .footer-top .footer-contact p {
    line-height: 26px;
  }
  .footer .copyright {
    text-align: center;
    padding-top: 30px;
    color: #012970;
  }
  .footer .credits {
    padding-top: 10px;
    text-align: center;
    font-size: 13px;
    color: #012970;
  }